<template>
</template>

<script>

import * as api from '@/data/api/market/index'

export default {
	data() {
		return {
			code: '',
			state: '',
			token: ''
		}
	},
	mounted() {
		let me=this
		me.session = localStorage
		me.code = me.$route.query.code
		me.state = JSON.parse(me.$route.query.state)
		if (me.state.name === 'login') {
			me.$router.push({
				path:'/user/login/index',
				query: {'code': me.code}
			})
			// api.getSsoLogin({phone: '', password: '', code: me.code}).then(res => {
			// 	if (res.code === 200) {
			// 		if (res.data.token) {
			// 			if (localStorage.getItem('token')) {
			// 				localStorage.removeItem('token')
			// 				localStorage.setItem('token', res.data.token)
			// 			} else {
			// 				localStorage.setItem('token', res.data.token)
			// 			}
			// 			if (me.state.url) {
			// 				me.$router.push({
			// 					path: me.state.url
			// 				})
			// 			} else {
			// 				me.$router.push({
			// 					path:'/user/home/index'
			// 				})
			// 			}
			// 		} else {
			// 			me.$router.push({
			// 				path:'/user/login/index'
			// 			})
			// 		}
			// 	} else {
			// 		me.$toast({
			// 			message: res.message,
			// 			icon: 'warning-o'
			// 		});
			// 	}
			// }).catch((res) => {
			// 	me.$toast({
			// 		message: res.message,
			// 		icon: 'warning-o'
			// 	});
			// }).finally(() => {
			// })
		} else if (me.state.name === 'home') {
			api.getSsoLogin({phone: '', password: '', code: me.code}).then(res => {
				if (res.code === 200) {
					if (res.data.token) {
						if (localStorage.getItem('token')) {
							localStorage.removeItem('token')
							localStorage.setItem('token', res.data.token)
						} else {
							localStorage.setItem('token', res.data.token)
						}
						if (me.state.url) {
							me.$router.push({
								path: me.state.url
							})
						} else {
							me.$router.push({
								path:'/maket/home/index'
							})
						}
					} else {
						me.$router.push({
							path:'/user/login/index'
						})
					}
				} else {
					me.$toast({
						message: res.message,
						icon: 'warning-o'
					});
				}
			}).catch((res) => {
				me.$toast({
					message: res.message,
					icon: 'warning-o'
				});
			}).finally(() => {
			})
		}
		// console.log("============>" + me.code + '-' + me.state)
		// if (me.code) {
		// 	api.authorizeCallback({'code': me.code, 'state': me.state}).then(res => {
    //     if (res.code === 200) {
